html {
    background: rgb(240, 240, 240);
    margin: 0;
    padding: 0;
    margin: -0.6%;
}

.splash {
    background: white;
    width: 1240px;
    height: auto;
    margin: 0 auto;
    font-family: "Overpass";
    min-width: 1240px;
    position: relative;
}

.position-absolute {
    position: absolute;
}

.position-relative {
    position: relative;
}

.overflow-hidden {
    overflow: hidden;
}

.splash-top-line {
    background: #be955c;
    height: 15px;
}

.splash-banner-navbar {
    display: flex;
    justify-content: space-between;
    position: absolute;
    z-index: 2;
    width: 1240px;
    padding: 20px;
    box-sizing: border-box;
}

.splash-banner-left {
    height: 32px;
}

.splash-banner-left img {
    height: 50px;
}

.splash-banner-right {
    display: flex;
}

.splash-banner-right button {
    padding: 3px;
    border-radius: 4px;
    margin-right: 9px;
    padding-left: 15px;
    padding-right: 15px;
    height: 31px;
    font-size: 14px;
    cursor: pointer;
}

.splash-banner {
    background: rgb(193, 140, 157);
    background-repeat: no-repeat;
    background-size: 100% 100%;
    height: 450px;
    /* padding-top: 19px;
    padding-left: 30px;
    padding-right: 30px; */
}

.splash-login {
    margin-right: 7px;
    background: transparent;
    border: 1px solid rgb(255 255 255);
    border-radius: 5px;
    color: rgb(255 255 255);
}

.splash-signup {
    background: #be955c;
    color: white;
    border: 1px solid #be955c;
}

.splash-banner-center {
    margin-left: 72px;
    text-align: center;
    width: 70%;
    margin-top: 100px;
}

.splash-banner-center h2 {
    font-size: 35px;
    margin-bottom: 15px;
    color: rgb(36, 36, 36);
    font-weight: 500;
}

.splash-banner-center p {
    width: 63%;
    height: 50px;
    margin: 0 auto;
    font-size: 17px;
    margin-bottom: 29px;
    line-height: 1.5;
}

.large-button-transparent {
    background: transparent;
    font-family: "Open Sans", sans-serif;
    border: 1px solid white;
    color: white;
    border-radius: 3px;
    padding: 11.5px 36px;
    margin-right: 25px;
    font-size: 18px;
    font-weight: 350;
    cursor: pointer;
    text-decoration: none;
}

.large-button-orange {
    background: #be955c;
    color: white;
    border: 1px solid #be955c;
    border-radius: 3px;
    padding: 13px 68px;
    font-size: 18px;
    font-weight: 350;
    height: 47px;
    cursor: pointer;
}


/* SEARCH */

.splash-search {
    display: flex;
    justify-content: center;
    line-height: 20px;
    margin-top: 30px;
    align-items: center;
    padding-top: 12px;
    margin-bottom: 30px;
}

.splash-search input {
    background: rgb(240, 240, 240);
    width: 520px;
    border: none;
    height: 45px;
    padding: 0;
    padding-left: 15px;
    padding-right: 15px;
    font-size: 16px;
    border-radius: 3px 0 0 3px;
}

.splash-search input::-webkit-input-placeholder {
    color: rgb(165, 165, 165);
}

.splash-search input:-ms-input-placeholder {
    color: rgb(165, 165, 165);
}

.splash-search input::placeholder {
    color: rgb(165, 165, 165);
}

.splash-search>button {
    background: rgb(240, 240, 240);
    margin-right: 18px;
    border: none;
    height: 45px;
    width: 50px;
    font-size: 19px;
    color: gray;
    border-radius: 0 3px 3px 0;
    cursor: pointer;
}

.splash-search button:last-child {
    margin: 0;
    margin-left: 18px;
    background: #be955c;
    color: white;
    border: 1px solid #be955c;
    border-radius: 3px;
    width: auto;
    font-size: 16px;
    padding-left: 22px;
    padding-right: 22px;
    cursor: pointer;
}

.splash-search p {
    line-height: 2.5px;
    font-size: 16px;
}


/* trending container */

.splash-trending-container {
    display: flex;
    flex-direction: column;
    width: 95%;
    margin: auto;
    align-items: center;
    font-family: "Open Sans", sans-serif;
    padding-bottom: 44px;
}

.splash-trending-container h3 {
    margin-bottom: 40px;
    font-weight: 400;
    font-size: 30px;
    font-weight: 300;
}

.splash-trending-tracks {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
}

.splash-trending-container img {
    height: 180px;
    width: 180px;
}

.splash-button-row {
    margin-top: 15px;
    /* justify-content: space-around; */
    margin-left: 459px;
}

.splash-trending-button {
    background: #be955c;
    color: white;
    border: 1px solid #be955c;
    border-radius: 3px;
    padding: 13px 30px;
    font-size: 18px;
    font-weight: 350;
    height: 47px;
    cursor: pointer;
}


/* splash mobile div */

.splash-mobile {
    display: flex;
    height: 450px;
    background: rgb(240, 240, 240);
    padding: 50px 70px 0px 70px;
    margin-bottom: -60px;
}

.mobile-img {
    background-repeat: no-repeat;
    background-size: 83% 87%;
    background-position: bottom;
    height: 100%;
    width: 70%;
