    width: 70px;
    background: linear-gradient(to right, rgb(0, 0, 0), rgb(255, 147, 165));
}

.mobile-text h3 {
    font-size: 23px;
    margin-top: 15px;
    margin-bottom: 30px;
    line-height: 1.5;
    width: 300px;
    font-weight: 100;
}


/* splash creator */

.splash-creators {
    background-image: url(/static/media/image1.14f8a456.jpg);
    background-repeat: no-repeat;
